import { observer } from "mobx-react";
import React, { Suspense, useContext, useEffect, useState } from "react";
import sleep from "sleep-promise";
import Spinner from "_components/common/Spinner";
import StoreContext from "_context/storeContext";

// @todo - remove this artifical slowing down bolierplate
const LoginPage = React.lazy(async () => {
  await sleep(1000);
  return import("_pages/LoginPage");
});
const DashboardPage = React.lazy(async () => {
  await sleep(2000);
  return import("_pages/DashboardPage");
});

const HomePage = React.lazy(async () => {
  await sleep(2000);
  return import("_pages/HomePage");
});

const MainPage: React.FC = () => {
  const store = useContext(StoreContext);

  const [isHandshaked, setIsHandshaked] = useState(false);

  useEffect(() => {
    console.log(
      "from Main page isAuthenticatedWithApi",
      store.appStore.isAuthenticatedWithApi
    );
    setIsHandshaked(store.appStore.isAuthenticatedWithApi);
  }, [store.appStore.isAuthenticatedWithApi]);

  let content: React.ReactElement = <div>Ops,there must be an error here!</div>;

  if (!isHandshaked) {
    content = (
      <LoginPage
        isHandshaked={store.appStore.isSocketConnected}
        ppNodeUrl={store.appStore.socketUrl}
      />
    );
  } else {
    content = (
      // <HomePage />
      <DashboardPage />
    );
  }

  return (
    <Suspense fallback={<Spinner extraClasses="is-full-height" />}>
      {content}
    </Suspense>
  );
};

export default observer(MainPage);
