export enum SocketMessagesMap {
  subscribe = "monitor_subscribe",
  getDiskUsage = "monitor_getDiskUsage",
  getNodeDetails = "monitor_getNodeDetails",
  getTrafficData = "monitor_getTrafficData",
  getPeerList = "monitor_getPeerList",
}

export interface SocketMessage<T> {
  id: number;
  method: string;
  params: T;
}

export interface SocketError {
  code: number;
  message: string;
}

export interface BasicSocketResponse<T> {
  error?: SocketError;
  originalMessage: T;
}

export interface ErrorSocketResponse<T> extends BasicSocketResponse<T> {
  error: SocketError;
}

export type AuthenticationMessageParameters = [string, string];
export type GetDiskUsageMessageParameters = [{ subid: string }];
export type GetNodeDetailsMessageParameters = [{ subid: string }];
export type GetPeerListMessageParameters = [{ subid: string }];
export type GetTrafficDataMessageParameters = [
  { subid: string; lines: number }
];

export interface AuthenticationSocketMessage
  extends SocketMessage<AuthenticationMessageParameters> {}
export interface GetDiskUsageSocketMessage
  extends SocketMessage<GetDiskUsageMessageParameters> {}
export interface GetNodeDetailsSocketMessage
  extends SocketMessage<GetNodeDetailsMessageParameters> {}
export interface GetPeerListSocketMessage
  extends SocketMessage<GetPeerListMessageParameters> {}
export interface GetTrafficDataSocketMessage
  extends SocketMessage<GetTrafficDataMessageParameters> {}
