import * as ResponseProcessor from "_src/services/processors/responseProcessor";
import * as Types from "_src/services/processors/types";
import { isMessage, isNotification } from "_src/services/processors/utils";

export const processSocketMessage = (
  originalMessageResponse: Types.SocketMessageResponse
) => {
  if (isMessage(originalMessageResponse)) {
    return ResponseProcessor.processMessageResponse(originalMessageResponse);
  }

  if (isNotification(originalMessageResponse)) {
    return ResponseProcessor.processNotificationResponse(
      originalMessageResponse
    );
  }

  return ResponseProcessor.processUknowResponse(originalMessageResponse, 4);
};
