import * as Types from "_src/services/processors/types";
import * as SocketTypes from "_src/services/socket/types";

export const isNotification = (
  response: Types.SocketMessageResponse
): response is Types.NotificationResponse => {
  return "params" in response;
};

export const isMessage = (
  response: Types.SocketMessageResponse
): response is Types.MessageResponse => {
  return "result" in response;
};

export const isAuthenticationResponse = (
  response: Types.MessageResponse
): response is Types.AuthenticationResponse => {
  return typeof response.result === "string";
};

export const isGetDiskUsageResult = (
  responseResult: Types.MessageResult
): responseResult is Types.DiskUsageResult => {
  return (
    "message_type" in responseResult &&
    responseResult.message_type === SocketTypes.SocketMessagesMap.getDiskUsage
  );
};

export const isNodeDetailsResult = (
  responseResult: Types.MessageResult
): responseResult is Types.NodeDetailsResult => {
  return (
    "message_type" in responseResult &&
    responseResult.message_type === SocketTypes.SocketMessagesMap.getNodeDetails
  );
};

export const isPeerListResult = (
  responseResult: Types.MessageResult
): responseResult is Types.PeerListResult => {
  return (
    "message_type" in responseResult &&
    responseResult.message_type === SocketTypes.SocketMessagesMap.getPeerList
  );
};

export const isTrafficInfoResult = (
  responseResult: Types.MessageResult
): responseResult is Types.TrafficInfoResult<Types.TrafficInfo[]> => {
  return (
    "message_type" in responseResult &&
    responseResult.message_type === SocketTypes.SocketMessagesMap.getTrafficData
  );
};

export const isNotificationResult = (
  paramsResult: Types.NotificationResult
): paramsResult is Types.NotificationResult => {
  return (
    Types.NotificationResultList.disk_usage in paramsResult &&
    Types.NotificationResultList.online_state in paramsResult &&
    Types.NotificationResultList.peer_list in paramsResult &&
    Types.NotificationResultList.traffic_info in paramsResult
  );
};

export const verifyFailedMessageResponse = (
  returnCode: number,
  messageType: string
) => {
  if (`${returnCode}` !== "0") {
    throw new Error(`PP node failed for "${messageType}"`);
  }
};
