import { Space, Spin } from "antd";
import classNames from "classnames";
import React from "react";
import "./index.scss";

type SpaceSize = "large" | "middle" | "small";
type SpinSize = "large" | "small" | "default" | undefined;

interface SimpleProps {
  spinSize?: SpinSize;
  spaceSize?: SpaceSize;
  extraClasses?: string;
}

const Spinner: React.FC<SimpleProps> = ({
  spinSize = "large",
  spaceSize = "large",
  extraClasses = "",
}) => {
  return (
    <div className={classNames("spinner-container", extraClasses)}>
      <Space size={spaceSize}>
        <Spin size={spinSize} />
      </Space>
    </div>
  );
};

export default Spinner;
