import React from "react";
import StoreContext from "_context/storeContext";
import AppStore from "./AppStore";

class RootStore {
  public appStore: AppStore;

  constructor() {
    this.appStore = new AppStore();
  }
}

const _rootStore: StratosNodeMonitor.RootStore<AppStore> = new RootStore();

export default _rootStore;

export const StoreProvider: React.FC = ({ children }) => {
  return (
    <StoreContext.Provider value={_rootStore}>{children}</StoreContext.Provider>
  );
};
