import { NodeMetricSlugs } from "_constants/nodeMetrics";
import { lookUpCountryCode } from "_services/geoIp";
import * as ProcessorTypes from "_services/processors/types";

interface FormatterReturnType {
  peersDiscoveredNodeMetric: StratosNodeMonitor.NodeMetric;
  connectedPeers: StratosNodeMonitor.ConnectedPeer[];
}

export const formatPeerListMessage = async (
  notificationResult: ProcessorTypes.IPeerResult
): Promise<FormatterReturnType> => {
  const { peer_list } = notificationResult;

  if (!peer_list) {
    throw new Error("Peer list was not found in the socket response");
  }

  const { total, peerlist } = peer_list;

  const peersDiscoveredNodeMetric = {
    id: "3",
    title: "Peers Discovered",
    slug: NodeMetricSlugs.PeersDiscovered,
    metricInfo: `${total}`,
    main: true,
  };

  if (total > 0 && !peerlist) {
    throw new Error("Peer list is not correct in the socket response");
  }

  if (!peerlist) {
    return { peersDiscoveredNodeMetric, connectedPeers: [] };
  }

  const connectedPeersPromises = peerlist.map(async (peerItem, idx) => {
    const { network_address, p2p_address, latency, connection } = peerItem;

    const [countryCode, location] = await lookUpCountryCode(network_address);

    const result = {
      id: `${idx + 1}`,
      location,
      countryCode,
      latency: `${latency}`,
      address: p2p_address,
      connection: [connection],
    };

    return result;
  });

  const connectedPeers = await Promise.all(connectedPeersPromises);

  return { peersDiscoveredNodeMetric, connectedPeers };
};
