import { notification } from "antd";
import * as Formatters from "_src/services/formatters";
import * as Types from "_src/services/processors/types";
import { verifyFailedMessageResponse } from "_src/services/processors/utils";
import {
  sendGetNodeDetailsMessage,
  sendGetTrafficDataMessage,
} from "_src/services/socket/sendMessage";
import * as SocketTypes from "_src/services/socket/types";

import RootStore from "_stores";

export const processAuthenticationMessage = (
  messageResponse: Types.AuthenticationResponse
) => {
  const appStore = RootStore.appStore;
  const { result: subid } = messageResponse;

  if (!appStore.subid && subid) {
    appStore.setSubid(subid);
    appStore.setIsAuthenticatedWithApi(true);

    // now sending node details message request
    sendGetNodeDetailsMessage(appStore.subid);
    sendGetTrafficDataMessage(appStore.subid, 20);
  }
};

export const processGetDiskUsageMessage = (result: Types.DiskUsageResult) => {
  const { disk_usage, return: messageReturn } = result;

  verifyFailedMessageResponse(
    messageReturn,
    SocketTypes.SocketMessagesMap.getDiskUsage
  );

  console.log("disk_usage !!", disk_usage);

  const dataHostingMetric = Formatters.formatDiskUsageMessage(result);
  console.log(
    "ðŸš€ ~ file: messagesProcessor.ts ~ line 34 ~ processGetDiskUsageMessage ~ dataHostingMetric",
    dataHostingMetric
  );
};

export const processNodeDetailsMessage = (result: Types.NodeDetailsResult) => {
  const appStore = RootStore.appStore;

  const { return: messageReturn } = result;

  verifyFailedMessageResponse(
    messageReturn,
    SocketTypes.SocketMessagesMap.getNodeDetails
  );

  const nodeDetails = Formatters.formatNodeDetailsMessage(result);
  const nodeInfo = { nodeDetails };

  appStore.setNodeInfo(nodeInfo);
};

export const processPeerListMessage = async (result: Types.PeerListResult) => {
  const appStore = RootStore.appStore;
  const { return: messageReturn } = result;

  verifyFailedMessageResponse(
    messageReturn,
    SocketTypes.SocketMessagesMap.getPeerList
  );

  const peerListInfo = await Formatters.formatPeerListMessage(result);

  const { connectedPeers } = peerListInfo;
  console.log(
    "ðŸš€ ~ file: messagesProcessor.ts ~ line 55 ~ processPeerListMessage ~ peerListInfo",
    peerListInfo
  );

  appStore.setConnectedPeers(connectedPeers);
};

export const processTrafficInfoMessage = (
  result: Types.TrafficInfoResult<Types.TrafficInfo[]>
) => {
  console.log(
    "ðŸš€ ~ file: messagesProcessor.ts ~ line 61 ~ processTrafficInfoMessage ~ result",
    result
  );
  const appStore = RootStore.appStore;

  const { traffic_info: trafficInfoList, return: messageReturn } = result;

  verifyFailedMessageResponse(
    messageReturn,
    SocketTypes.SocketMessagesMap.getTrafficData
  );

  trafficInfoList.forEach((trafficInfo) => {
    const myTrafficInfo = Formatters.formatTrafficInfoMessage({
      traffic_info: trafficInfo,
    });
    console.log(
      "ðŸš€ ~ file: messagesProcessor.ts ~ line 79 ~ processTrafficInfoMessage ~ myTrafficInfo",
      myTrafficInfo
    );

    appStore.updateTrafficInfo(myTrafficInfo);
  });
};

export const processNotificationMessage = async (
  result: Types.NotificationResult
) => {
  const appStore = RootStore.appStore;
  console.log("result from notification", result);

  const metricsList = [];

  try {
    const dataHostingMetric = Formatters.formatDiskUsageMessage(result);
    metricsList.push(dataHostingMetric);
  } catch (err) {
    notification.error({
      message: "Could not format data hosting info",
      description: `${(err as Error).message}`,
    });
  }

  try {
    const peerListInfo = await Formatters.formatPeerListMessage(result);

    const { peersDiscoveredNodeMetric, connectedPeers } = peerListInfo;
    metricsList.push(peersDiscoveredNodeMetric);

    appStore.setConnectedPeers(connectedPeers);
  } catch (err) {
    notification.error({
      message: "Could not format peer list info",
      description: `${(err as Error).message}`,
    });
  }

  try {
    const myTrafficInfo = Formatters.formatTrafficInfoMessage(result);

    appStore.updateTrafficInfo(myTrafficInfo);
  } catch (error) {
    notification.error({
      message: "Could not format traffic info",
      description: `${(error as Error).message}`,
    });
  }

  try {
    const metrics = Formatters.formatOnlineStateMessage(result);
    const [onlineStatusMetric, onlineSinceMetric, onlineForMetric] = metrics;

    metricsList.push(onlineStatusMetric);
    metricsList.push(onlineSinceMetric);
    metricsList.push(onlineForMetric);
  } catch (err) {
    notification.error({
      message: "Could not format online data metrics info",
      description: `${(err as Error).message}`,
    });
  }

  if (metricsList.length) {
    const updatedMetricsList = {
      metricsList,
    };

    appStore.setNodeMetrics(updatedMetricsList);
  }
};
