import { useState } from "react";

type HandlerType<T> = (newSortName: T) => () => void;

type HookReturnType<T> = [T, HandlerType<T>];

export function useChangeSort<T>(defaultSortName: T): HookReturnType<T> {
  const [sortName, setSortName] = useState(defaultSortName);

  const sortBy = (newSortName: T) => {
    return () => {
      setSortName(newSortName);
    };
  };

  return [sortName, sortBy];
}
