import { NodeMetricSlugs } from "_constants/nodeMetrics";
import * as ProcessorTypes from "_services/processors/types";
import { formatDate } from "_services/utils";

export const formatOnlineStateMessage = (
  notificationResult: ProcessorTypes.NotificationResult
): StratosNodeMonitor.NodeMetric[] => {
  const { online_state } = notificationResult;

  if (!online_state) {
    throw new Error("Online status data was not found in the socket response");
  }

  const { online, since } = online_state;
  const onlineStatusMetricInfo = online ? "Online" : "Offline";

  const { since: onlineSinceMetricInfo, runningFor: onlineForMetricInfo } =
    formatDate(since);

  return [
    {
      id: "1",
      title: "Status",
      slug: NodeMetricSlugs.Status,
      metricInfo: onlineStatusMetricInfo,
      main: true,
    },
    {
      id: "4",
      title: "Online Since",
      slug: NodeMetricSlugs.OnlineSince,
      metricInfo: onlineSinceMetricInfo,
      main: true,
    },
    {
      id: "6",
      title: "Online For",
      slug: NodeMetricSlugs.OnlineFor,
      metricInfo: onlineForMetricInfo,
      main: false,
    },
  ];
};
