import * as ProcessorTypes from "_src/services/processors/types";
import RootStore from "_stores";

export const formatNodeDetailsMessage = (
  nodeDetailsResult: ProcessorTypes.NodeDetailsResult
): StratosNodeMonitor.NodeDetails => {
  const { appStore } = RootStore;
  const { nodeDetails: defaultNodeDetails } = appStore.nodeInfo;

  const { node_details: processedNodeDetails } = nodeDetailsResult;

  const { id, address } = processedNodeDetails;

  return { ...defaultNodeDetails, id, address };
};
