import W3CWebSocket from "websocket";
import * as ProcessorTypes from "_src/services/processors/types";
import * as SocketTypes from "_src/services/socket/types";

type ParsedMessage =
  | SocketTypes.ErrorSocketResponse<W3CWebSocket.IMessageEvent>
  | SocketTypes.BasicSocketResponse<ProcessorTypes.SocketMessageResponse>;

export const isParsedWithError = (
  message: ParsedMessage
): message is SocketTypes.ErrorSocketResponse<W3CWebSocket.IMessageEvent> => {
  return "error" in message;
};

export const ifMessageHasAnError = (
  message: ProcessorTypes.SocketMessageResponse
): message is ProcessorTypes.SocketMessageResponse => {
  return "error" in message;
};

export const parseEventData = (evt: W3CWebSocket.IMessageEvent) => {
  const eventData = evt?.data?.toString();

  if (!eventData) {
    const parsedMessage: SocketTypes.ErrorSocketResponse<typeof evt> = {
      error: {
        code: -161616,
        message: "socket response has no data in the message event",
      },
      originalMessage: evt,
    };

    return parsedMessage;
  }

  try {
    const message: ProcessorTypes.SocketMessageResponse = JSON.parse(eventData);

    const parsedMessage: SocketTypes.BasicSocketResponse<typeof message> = {
      originalMessage: message,
    };

    const hasErrorA = ifMessageHasAnError(message);

    if (hasErrorA) {
      parsedMessage.error = (
        message as unknown as SocketTypes.ErrorSocketResponse<typeof evt>
      ).error;
    }
    return parsedMessage;
  } catch (err) {
    const parsedMessage: SocketTypes.ErrorSocketResponse<typeof evt> = {
      error: {
        code: -131313,
        message: "could not parse message from the socket",
      },
      originalMessage: evt,
    };

    return parsedMessage;
  }
};
