import { NodeMetricSlugs } from "_constants/nodeMetrics";
import * as ProcessorTypes from "_services/processors/types";
import { formatTrafficValue } from "_services/utils";

export const formatDiskUsageMessage = (
  notificationResult: ProcessorTypes.IDiskUsageResult
): StratosNodeMonitor.NodeMetric => {
  const { disk_usage } = notificationResult;

  if (!disk_usage) {
    throw new Error("Disk usage was not found in the socket response");
  }

  const { data_host } = disk_usage;

  const dataHostingMetricInfo = formatTrafficValue(data_host, false);

  return {
    id: "2",
    title: "Data Hosting",
    slug: NodeMetricSlugs.DataHosting,
    metricInfo: dataHostingMetricInfo,
    main: true,
  };
};
