export const flatten = <T>(array: T[]) => ([] as T[]).concat(...array);

export const random = (min: number, max: number) =>
  Math.floor(Math.random() * (max - min) + min);

export const range = (start: number, end: number) =>
  Array.from(" ".repeat(end - start), (_, index) => start + index);

export const shuffle = <T>(values: T[]) =>
  values
    .map((value) => [Math.random(), value] as const)
    .sort(([left], [right]) => left - right)
    .map(([, value]) => value);

export const getTimestamp = () => new Date().getTime();

export const formatTrafficValue = (speedInBytes: number, isItSpeed = true) => {
  const speedInKb = speedInBytes / 1024;
  const speedInMb = speedInKb / 1024;
  const speedInGb = speedInMb / 1204;

  const suffix = isItSpeed ? "/s" : "";

  const isKb = speedInKb >= 1 && speedInMb < 1;

  if (isKb) {
    return `${speedInKb.toFixed(1)} Kb${suffix}`;
  }

  const isMb = speedInMb >= 1 && speedInGb < 1;

  if (isMb) {
    return `${speedInMb.toFixed(1)} Mb${suffix}`;
  }

  const isGb = speedInGb >= 1;

  if (isGb) {
    return `${speedInGb.toFixed(1)} Gb${suffix}`;
  }

  return speedInBytes + " B/s";
};

export const getRandomNumber = (min = 1, max = 10) =>
  Math.floor(Math.random() * (max - min + 1)) + min;

export const makeRepeated = (
  arr: StratosNodeMonitor.ConnectedPeer[],
  repeats: number
) =>
  Array.from(
    {
      length: repeats,
    },
    () => arr
  ).flat();

export const dateDiffWith = (dateString: string, type = "minute") => {
  const _today = new Date();
  const pastDate = new Date(dateString);

  const diffMs = _today.getTime() - pastDate.getTime(); // milliseconds between pastDate and now
  const diffDays = Math.floor(diffMs / 86400000); // days
  const diffHrs = Math.floor((diffMs % 86400000) / 3600000); // hours
  const diffMins = Math.floor(((diffMs % 86400000) % 3600000) / 60000); // minutes
  const diffSec = Math.round(
    (((((diffMs % 86400000) % 3600000) % 60000) / 600) * 60) / 100
  ); // sec

  const result = {
    day: diffDays,
    hour: diffHrs,
    minute: diffMins,
    sec: diffSec,
    ms: diffMs,
  };

  return result;
};

export const formatDate = (timestamp: number) => {
  const startDate = new Date(timestamp * 1000);
  const formattedTime = dateDiffWith(startDate.toString());
  const { sec, day, hour, minute } = formattedTime;
  const since = startDate.toLocaleString();

  const runningFor = `${day} days ${hour} hours ${minute} mins ${sec} sec`;

  return {
    since,
    runningFor,
  };
};
