import { SocketMessagesMap } from "_src/services/socket/types";

export enum NotificationResultList {
  disk_usage = "disk_usage",
  online_state = "online_state",
  peer_list = "peer_list",
  traffic_info = "traffic_info",
}

// Info (data entities)
export interface DiskUsageInfo {
  data_host: number;
}

export interface NodeDetailsInfo {
  id: string;
  address: string;
}

export interface OnlineStateInfo {
  online: boolean;
  since: number;
}

export interface PeerItemInfo {
  network_address: string;
  p2p_address: string;
  status: number;
  latency: number;
  connection: string;
}

export interface PeerListInfo {
  total: number;
  peerlist: PeerItemInfo[];
}

export interface TrafficInfo {
  traffic_inbound: number;
  traffic_outbound: number;
  time_stamp: string;
}
// end of info

// messages response results (has info items)
export type AuthenticationResult = string;

export interface IDiskUsageResult {
  disk_usage: DiskUsageInfo;
}

export interface DiskUsageResult extends IDiskUsageResult {
  message_type: SocketMessagesMap.getDiskUsage;
  return: number;
}

export interface NodeDetailsResult {
  message_type: SocketMessagesMap.getNodeDetails;
  node_details: NodeDetailsInfo;
  return: number;
}

export interface IPeerResult {
  peer_list: PeerListInfo;
}

export interface PeerListResult extends IPeerResult {
  message_type: SocketMessagesMap.getPeerList;
  return: number;
}

export interface ITrafficInfoResult<T> {
  traffic_info: T;
}

export interface TrafficInfoResult<K> extends ITrafficInfoResult<K> {
  message_type: SocketMessagesMap.getTrafficData;
  return: number;
}
// end of messages response results

// messages responses
export interface AuthenticationResponse {
  result: AuthenticationResult;
}
export interface DiskUsageResponse {
  result: DiskUsageResult;
}

export interface NodeDetailsResponse {
  result: NodeDetailsResult;
}

export interface TrafficInfoResponse {
  result: TrafficInfoResult<TrafficInfo[]>;
}

export interface PeerListResponse {
  result: PeerListResult;
}
// end of messages responses

// notification result and response
export interface NotificationResult
  extends IPeerResult,
    IDiskUsageResult,
    ITrafficInfoResult<TrafficInfo> {
  [NotificationResultList.disk_usage]: DiskUsageInfo;
  [NotificationResultList.online_state]: OnlineStateInfo;
  [NotificationResultList.peer_list]: PeerListInfo;
  [NotificationResultList.traffic_info]: TrafficInfo;
}

export interface NotificationParams {
  result: NotificationResult;
}

export interface NotificationResponse {
  params: NotificationParams;
}
// notification result and response

export type MessageResponse =
  | AuthenticationResponse
  | DiskUsageResponse
  | NodeDetailsResponse
  | PeerListResponse
  | TrafficInfoResponse;

export type SocketMessageResponse = NotificationResponse | MessageResponse;

// only those with message_type
export type MessageResult =
  | DiskUsageResult
  | NodeDetailsResult
  | PeerListResult
  | TrafficInfoResult<TrafficInfo[]>;
