import { useState } from "react";

type HandlerType = () => void;

type HookReturnType<T> = [T, HandlerType];

export function useSortDirectionToggle<T>(
  sortAscending: T,
  sortDescending: T
): HookReturnType<T> {
  const [sortDirection, setSortDirection] = useState(sortAscending);

  const changeSortDirection = () => {
    setSortDirection((previousDirection: T) =>
      previousDirection === sortAscending ? sortDescending : sortAscending
    );
  };

  return [sortDirection, changeSortDirection];
}
