import * as ProcessorTypes from "_services/processors/types";

export const formatTrafficInfoMessage = (
  result: ProcessorTypes.ITrafficInfoResult<ProcessorTypes.TrafficInfo>
): StratosNodeMonitor.TrafficInfo => {
  const { traffic_info } = result;

  const { traffic_inbound, traffic_outbound, time_stamp } = traffic_info;

  return {
    id: "1",
    time: time_stamp,
    inbound: `${traffic_inbound}`,
    outbound: `${traffic_outbound}`,
  };
};
