import * as CreateFactory from "_src/services/socket/createMessage";
import * as Types from "_src/services/socket/types";

import RootStore from "_stores";

export const sendMessageToSocket = <T extends Types.SocketMessage<K>, K>(
  message: T
) => {
  const appStore = RootStore.appStore;

  const [
    _ourDataFromSocket,
    _connectToSocketHandler,
    _disconnectFromSocketHandler,
    sendMessageHandler,
  ] = appStore.socketData;

  sendMessageHandler(message);
};

export const sendAuthenticationMessage = (token: string) => {
  const message = CreateFactory.createAuthenticationMessage(token);
  type paramsType = typeof message["params"];
  return sendMessageToSocket<typeof message, paramsType>(message);
};

