import {
  action,
  makeAutoObservable,
  observable,
  runInAction,
  toJS,
} from "mobx";
import { chartDataLength, Directions } from "_constants/traficInfo";
import { formatTrafficValue } from "_src/services/utils";

export const defaultNodeInfo: StratosNodeMonitor.NodeInfo = {
  nodeDetails: {
    id: "",
    address: "",
  },
};

export const defaultNodeMetrics: StratosNodeMonitor.NodeMetrics = {
  metricsList: [],
};

const defaultTrafficInfo = {
  id: "",
  time: "",
  inbound: "",
  outbound: "",
} as const;

const defaultTrafficMetric = {
  direction: Directions.Inbound,
  current: "",
  max: "",
};
type ConnectHandlerType = (url?: string) => void;
type DisconnectHandlerType = () => void;
type SendMessageHandlerType = (
  data: StratosNodeMonitor.AbstractObject
) => boolean;

type HookReturnType = [
  StratosNodeMonitor.AbstractObject,
  ConnectHandlerType,
  DisconnectHandlerType,
  SendMessageHandlerType
];

const emptyFunc = () => {};

const defaultSocketData: HookReturnType = [
  {},
  emptyFunc,
  emptyFunc,
  () => {
    return false;
  },
];

class AppStore {
  public connectedPeers: StratosNodeMonitor.ConnectedPeer[] = [];
  public nodeInfo: StratosNodeMonitor.NodeInfo = defaultNodeInfo;
  public nodeMetrics: StratosNodeMonitor.NodeMetrics = defaultNodeMetrics;

  public inboundTrafficMetric: StratosNodeMonitor.TrafficMetric =
    defaultTrafficMetric;
  public outboundTrafficMetric: StratosNodeMonitor.TrafficMetric =
    defaultTrafficMetric;

  public inboundSpeed: number = 0;
  public outboundSpeed: number = 0;

  public maxInboundSpeed: number = 0;
  public maxOutboundSpeed: number = 0;
  public trafficInfo: StratosNodeMonitor.TrafficInfo = defaultTrafficInfo;
  public trafficDataLines: StratosNodeMonitor.TrafficDataLine[] = [];

  public outboundTrafficDataLines: StratosNodeMonitor.TraficInfoDataItem[] = [];
  public inboundTrafficDataLines: StratosNodeMonitor.TraficInfoDataItem[] = [];

  public socketData: HookReturnType = defaultSocketData;
  public socketUrl: string = "";

  // auth
  public socketError: string = "";
  public subid = "";
  public isSocketConnected: boolean = false;
  public isAuthenticatedWithApi: boolean = false;
  public resolvedIpList: StratosNodeMonitor.AbstractObject = {};

  constructor() {
    makeAutoObservable(this, {
      init: action,
      connectedPeers: observable,
      setConnectedPeers: action,
      nodeInfo: observable,
      setNodeInfo: action,
      nodeMetrics: observable,
      setNodeMetrics: action,
      setSocketData: action,
      socketData: observable,

      setSocketUrl: action,
      socketUrl: observable,
      /// auth
      setSocketError: action,
      setSubid: action,
      setIsSocketConnected: action,
      setIsAuthenticatedWithApi: action,

      socketError: observable,
      subid: observable,
      isSocketConnected: observable,
      isAuthenticatedWithApi: observable,

      resolvedIpList: observable,
      isIpResolved: action,
      setIsIpResolved: action,
    });

    this.init();
  }

  async init() {
    this.logOut();
  }

  setSocketData(data: HookReturnType): void {
    this.socketData = data;
  }

  setSocketUrl(url: string): void {
    this.socketUrl = url;
  }

  logOut(): void {
    runInAction(() => {
      // this.authenticationToken = "";
      this.socketError = "";
      this.subid = "";
      this.isSocketConnected = false;
      this.isAuthenticatedWithApi = false;
    });
  }

  // setAuthenticationToken(token: string): void {
  //   this.authenticationToken = token;
  // }

  setSocketError(message: string): void {
    this.socketError = message;
  }

  setSubid(subid: string): void {
    this.subid = subid;
  }

  setIsSocketConnected(flag: boolean): void {
    this.isSocketConnected = flag;
  }

  setIsAuthenticatedWithApi(flag: boolean): void {
    this.isAuthenticatedWithApi = flag;
  }

  updateMaxInboundSpeed(speed: number): void {
    this.maxInboundSpeed =
      speed > this.maxInboundSpeed ? speed : this.maxInboundSpeed;
  }

  updateMaxOutboundSpeed(speed: number): void {
    this.maxOutboundSpeed =
      speed > this.maxOutboundSpeed ? speed : this.maxOutboundSpeed;
  }

  setInboundSpeed(speed: number): void {
    this.inboundSpeed = speed;
    this.updateMaxInboundSpeed(speed);
  }

  setOutboundSpeed(speed: number): void {
    this.outboundSpeed = speed;
    this.updateMaxOutboundSpeed(speed);
  }

  updateInboundTrafficMetric(current: number, max: number): void {
    const currentFormatted = formatTrafficValue(current);
    const maxFormatted = formatTrafficValue(max);

    this.inboundTrafficMetric = {
      direction: Directions.Inbound,
      current: currentFormatted,
      max: maxFormatted,
    };
  }

  updateOutboundTrafficMetric(current: number, max: number): void {
    const currentFormatted = formatTrafficValue(current);
    const maxFormatted = formatTrafficValue(max);

    this.outboundTrafficMetric = {
      direction: Directions.Outbound,
      current: currentFormatted,
      max: maxFormatted,
    };
  }

  updateInboundTrafficDataLines(x: string, y: number): void {
    const traficInfoDataItem = { x, y };
    const data = this.inboundTrafficDataLines.filter(
      (element) => element.x !== x
    );
    data.splice(0, 0, traficInfoDataItem);
    this.inboundTrafficDataLines = data.slice(0, chartDataLength);
  }

  updateOutboundTrafficDataLines(x: string, y: number): void {
    const traficInfoDataItem = { x, y: y * -1 };
    const data = this.outboundTrafficDataLines.filter(
      (element) => element.x !== x
    );
    data.splice(0, 0, traficInfoDataItem);
    this.outboundTrafficDataLines = data.slice(0, chartDataLength);
  }

  updateTrafficDataLines(
    inboundTrafficDataLines: StratosNodeMonitor.TraficInfoDataItem[],
    outboundTrafficDataLines: StratosNodeMonitor.TraficInfoDataItem[]
  ): void {
    this.trafficDataLines = [
      {
        id: Directions.Outbound,
        data: outboundTrafficDataLines,
      },
      {
        id: Directions.Inbound,
        data: inboundTrafficDataLines,
      },
    ];
  }

  // called from socket
  updateTrafficInfo(trafficInfo: StratosNodeMonitor.TrafficInfo): void {
    const { inbound, outbound, time } = trafficInfo;
    let newInbound = 0;
    let newOutbound = 0;

    try {
      newInbound = parseInt(inbound);
    } catch (error) {
      console.log("error parsing inbound!", error);
    }

    try {
      newOutbound = parseInt(outbound);
    } catch (error) {
      console.log("error parsing outbound!", error);
    }

    this.setInboundSpeed(newInbound);
    this.setOutboundSpeed(newOutbound);

    this.updateInboundTrafficMetric(this.inboundSpeed, this.maxInboundSpeed);
    this.updateOutboundTrafficMetric(this.outboundSpeed, this.maxOutboundSpeed);

    this.updateInboundTrafficDataLines(time, newInbound);
    this.updateOutboundTrafficDataLines(time, newOutbound);

    this.updateTrafficDataLines(
      this.inboundTrafficDataLines,
      this.outboundTrafficDataLines
    );
  }

  // called from socket
  setConnectedPeers(connectedPeers: StratosNodeMonitor.ConnectedPeer[]): void {
    if (!connectedPeers) {
      return;
    }
    this.connectedPeers = connectedPeers;
  }

  // called from withApi AND (to substitute) from useSocketData
  setNodeInfo(nodeInfo: StratosNodeMonitor.NodeInfo): void {
    this.nodeInfo = nodeInfo;
  }

  get mainNodeMetrics(): StratosNodeMonitor.NodeMetrics {
    const mainMetricsList = this.nodeMetrics.metricsList.filter(
      (metricItem) => metricItem.main === true
    );

    return { metricsList: mainMetricsList };
  }

  setNodeMetrics(nodeMetrics: StratosNodeMonitor.NodeMetrics): void {
    this.nodeMetrics = nodeMetrics;
  }

  isIpResolved(ip: string) {
    if (!ip) {
      return null;
    }
    return this.resolvedIpList[ip];
  }

  setIsIpResolved(ip: string, countryCode: string) {
    if (!this.isIpResolved(ip)) {
      this.resolvedIpList[ip] = "";
    }

    this.resolvedIpList[ip] = countryCode;
  }
}

export default AppStore;
