import { notification } from "antd";
import { useCallback, useEffect, useState } from "react";
import W3CWebSocket from "websocket";
import {
  isParsedWithError,
  parseEventData,
} from "_src/services/parser/rpcSocketDataParser";

import { processSocketMessage } from "_src/services/processors/mainProcessor";
import RootStore from "_stores";

type ConnectHandlerType = (url?: string) => void;
type DisconnectHandlerType = () => void;
type SendMessageHandlerType = (
  data: StratosNodeMonitor.AbstractObject
) => boolean;

type HookReturnType = [
  StratosNodeMonitor.AbstractObject,
  ConnectHandlerType,
  DisconnectHandlerType,
  SendMessageHandlerType
];

type HookType = (url: string) => HookReturnType;

// retry socket connection
const defaultRetry = 0;

const retryInterval = 1500;

export const useSocketData: HookType = (url) => {
  const appStore = RootStore.appStore;
  useEffect(() => {
    if (appStore.socketUrl) {
      console.log(`new url is !! ${appStore.socketUrl}`);
    }
  }, [appStore.socketUrl]);
  // send message function
  const [sendMessageHandler, setSendMessageHandler] =
    useState<SendMessageHandlerType>(
      () => (_data: StratosNodeMonitor.AbstractObject) => false
    );

  const [retry, setRetry] = useState(defaultRetry);

  const [parsedData, setParsedData] =
    useState<StratosNodeMonitor.AbstractObject>({});

  const [ourSocket, setOurSocket] = useState<W3CWebSocket.w3cwebsocket>();

  const disconnectFromSocketHandler: DisconnectHandlerType = () => {
    if (ourSocket && ourSocket.OPEN) {
      // so we don't want to re-connect
      setRetry(0);

      ourSocket.close();
      appStore.logOut();
    }
  };

  const connectToSocketHandler: ConnectHandlerType = (socketUrl) => {
    try {
      const ws = new W3CWebSocket.w3cwebsocket(socketUrl || url);

      ws.onopen = () => {
        console.log("connected to socket (from hook, connection is opened)");
        if (ws && ws.OPEN) {
          appStore.logOut();
          appStore.setIsSocketConnected(true);
          setOurSocket(ws);
        }

        // function to send messages to the socket
        setSendMessageHandler(() => {
          return (data: StratosNodeMonitor.AbstractObject) => {
            console.log("from handler!!");
            try {
              const messageToSend = JSON.stringify(data);
              console.log(
                "ðŸš€ ~ file: useSocketData.ts ~ line 70 ~ return ~ messageToSend",
                messageToSend
              );
              ws.send(messageToSend);
              return true;
            } catch (err) {
              return false;
            }
          };
        });
      };

      ws.onmessage = (evt) => {
        const parsedMessage = parseEventData(evt);

        const hasError = isParsedWithError(parsedMessage);

        if (hasError) {
          const { error } = parsedMessage;
          const { code, message } = error;
          console.log("socker - we have an error", error);
          console.log("parsedMessage is", parsedMessage);
          console.log("event in the socket response", evt);
          appStore.setSocketError(`${code} -  ${message}`);
          // that should be optional? proper error handling
          // appStore.logOut();
          return;
        }

        const { originalMessage } = parsedMessage;

        try {
          processSocketMessage(originalMessage);
        } catch (error) {
          console.log("error after processing", error);

          const { message: socketErrorMessage } = error as Error;

          notification.error({
            message: "Socket Error",
            description: `${socketErrorMessage}`,
          });
        }
      };

      ws.onclose = () => {
        console.log("disconnected");

        if (retry > 0) {
          setTimeout(() => {
            setRetry((retry) => retry - 1);
          }, retryInterval);
        }
      };
    } catch (err) {
      console.log("error trying to create an instace of ws", err);

      appStore.setIsSocketConnected(false);

      notification.error({
        message: "Socket Error",
        description: `${(err as Error).message}`,
      });

      appStore.setSocketError(`${(err as Error).message}`);
      return;
    }
  };

  const connectToSocketHandlerCallback = useCallback((socketUrl) => {
    connectToSocketHandler(socketUrl);
  }, []);

  const disconnectFromSocketHandlerCallback = useCallback(() => {
    disconnectFromSocketHandler();
  }, []);

  useEffect(() => {
    console.log("first effect in the socket");

    console.log("appStore.socketUrl", appStore.socketUrl);
    if (appStore.socketUrl) {
      console.log(`we have url. connecting to ${appStore.socketUrl}`);
      setTimeout(() => {
        connectToSocketHandlerCallback(appStore.socketUrl);
      }, 3000);
    }
    return () => {
      disconnectFromSocketHandlerCallback();
    };
  }, [appStore.socketUrl]);

  return [
    parsedData,
    connectToSocketHandler,
    disconnectFromSocketHandler,
    sendMessageHandler,
  ];
};
