import * as MessagesProcessor from "_src/services/processors/messagesProcessor";
import * as Types from "_src/services/processors/types";
import * as Utils from "_src/services/processors/utils";

export const processUknowResponse = (
  originalMessageResponse: Types.SocketMessageResponse,
  code: number
) => {
  console.log(
    "we got an unknow message or notification response. we dont know what to do. here it is",
    originalMessageResponse
  );

  console.log("code", code);
};

export const processMessageResponse = (
  messageResponse: Types.MessageResponse
) => {
  if (Utils.isAuthenticationResponse(messageResponse)) {
    return MessagesProcessor.processAuthenticationMessage(messageResponse);
  }

  const { result } = messageResponse;

  if (Utils.isGetDiskUsageResult(result)) {
    return MessagesProcessor.processGetDiskUsageMessage(result);
  }

  if (Utils.isNodeDetailsResult(result)) {
    return MessagesProcessor.processNodeDetailsMessage(result);
  }

  if (Utils.isPeerListResult(result)) {
    return MessagesProcessor.processPeerListMessage(result);
  }

  if (Utils.isTrafficInfoResult(result)) {
    return MessagesProcessor.processTrafficInfoMessage(result);
  }

  return processUknowResponse(messageResponse, 1);
};

export const processNotificationResponse = (
  messageResponse: Types.NotificationResponse
) => {
  const messageResult = messageResponse?.params?.result;

  const isNotificationResult = Utils.isNotificationResult(messageResult || {});

  if (!isNotificationResult) {
    return processUknowResponse(messageResponse, 2);
  }

  return MessagesProcessor.processNotificationMessage(messageResult);
};
