import { geoIpApiUrl } from "_constants/general";

import RootStore from "_stores";

export const lookUpCountryCode = async (ip: string) => {
  const appStore = RootStore.appStore;
  const [address] = ip.split(":");

  const resolvedCountryCode = appStore.isIpResolved(address);

  if (resolvedCountryCode) {
    return [resolvedCountryCode, ip];
  }

  try {
    const url = `${geoIpApiUrl}${address}`;
    const res = await fetch(url);
    const geoInfoData = await res.json();
    const { countryCode = "ZZ" } = geoInfoData;

    appStore.setIsIpResolved(address, countryCode);

    return [countryCode, ip];
  } catch (error) {
    return ["ZZ", ip];
  }
};
